<template>
    <div class="aquiOh" @click="cricouCod()">
        <img :src="GoogleChartAPI" alt="meu_qr">
    </div>
</template>

<script>

export default {
    data() {
        return {
            qrText: "https://phsantosalves.github.io/sus_front_celular_producao",
            width: 500,
            height: 500,
            GoogleChartAPI: null
        }
    },
    mounted() {
        this.GerarQRCode()
    },
    methods: {
        cricouCod() {
            this.$router.push('/triagem')
        },
        GerarQRCode() {
            this.GoogleChartAPI = `https://chart.googleapis.com/chart?cht=qr&chs=${this.width}x${this.height}&chld=H&chl=${this.qrText}`;
        }
    },
}
</script>

<style scoped>
.aquiOh {
    background-color: aliceblue;
    height: 100%;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.aquiOh img {
    border-radius: 20px;
}

.meuqr {
    transform: scaleX(5) scaleY(5);
}
</style>